* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  cursor: none;
  background-color: #020202;
  position: relative;
  overflow: hidden;
}

.container {
  width: 200vw;
  display: flex;
}

canvas {
  width: 100vw;
  height: 100vh;
}

.cliner {
  z-index: 100;
  background-color: #000;
  width: 100vw;
  height: 100vh;
  display: flex;
  position: relative;
}

.cliner > div {
  background: none;
  flex-grow: 1;
  height: 140vh;
  position: relative;
  overflow: hidden;
  transform: translateY(-40vh);
}

.cliner > div > img {
  width: 100%;
  height: 30%;
  position: absolute;
  top: -15px;
  transform: rotate(0);
}

.cliner img {
  position: absolute;
  bottom: 0;
  left: 0;
  transform: rotate(10deg);
}

img {
  width: 100px;
  height: 200px;
}

video {
  z-index: -1;
  width: 110%;
  position: absolute;
}

section {
  color: #faebd7;
  z-index: -1;
  flex-direction: column;
  display: flex;
  position: absolute;
  inset: 0;
}

#title {
  text-align: center;
  color: #793ea5;
  flex-grow: 1;
  width: 100%;
  padding-top: 1rem;
  font-family: Bagel Fat One, cursive;
  font-size: 3rem;
  position: relative;
}

#subtitle {
  color: #fec400;
  text-align: right;
  flex-grow: 1;
  padding-right: 10%;
  font-family: Courgette, cursive;
  font-size: 1.7rem;
}

#pageContent {
  flex-grow: 10;
  width: 45%;
  margin-left: 5%;
  font-family: Poppins, sans-serif;
  font-size: 1.1rem;
}

#contentTitle {
  color: #793ea5;
  flex-grow: 1;
  margin-top: 5%;
  margin-left: 5%;
  font-family: Bagel Fat One, cursive;
  font-size: 2rem;
  font-style: italic;
}

.specific {
  color: #fec400;
  font-weight: 800;
}

.mark {
  color: #fec400;
  margin-top: .1rem;
  font-style: italic;
}

.underline > div {
  color: #793ea5;
  border-bottom: 1px solid #793ea5;
  font-style: italic;
}

.wordDiv {
  margin-right: 8px;
}

.loadingContent {
  z-index: 1000000000;
  background: #555;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
}

.loadingContent > label {
  color: #ccc;
  font-size: 2rem;
}

.hidden {
  display: none;
}

.loadingContent > svg {
  width: 100%;
}

.loadingContent > svg > text {
  stroke: #0075ff;
  stroke-width: 5px;
  stroke-dasharray: 2550;
  stroke-dashoffset: 2550px;
  z-index: 10000;
}

@media (orientation: landscape) {
  .loadingContent {
    display: none;
  }
}

@media (width <= 768px) {
  #title {
    font-size: 2rem;
  }

  #subtitle {
    font-size: 1.2rem;
  }

  #pageContent {
    font-size: .8rem;
  }

  #contentTitle {
    margin-top: 3%;
    font-size: 1.5rem;
  }
}

